.wrapper {
  min-width: 960px;
    margin: 0 auto;
    background-color: #1C0028;
  }
  
  .faq {
    width: 100%;
    max-width: 960px;  /* Use max-width to limit the container size */
    margin: 0 auto;
    padding: 4rem 1rem;  /* Adjust padding for responsiveness */
    

  }
  
  .wrapper h2{
    font-size: 2rem;
    padding-top: 4%;
    text-align: center;
    color: white;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  }
  
  .faq-item {
    background-color: #f5f5f5;
    margin-bottom: 10px;
    cursor: pointer;
    padding: 20px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    background-color: white;
    font-family:Arial, Helvetica, sans-serif;
  }
  
  .faq-item:hover {
    background-color: #6E88FF;
  }
  
  .faq-item:hover .question{
    color:#1C0028;
  }
  
  .question {
    font-weight: bold;
    font-size: medium;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color: #6E88FF;
  }
  
  
  .answer {
    margin-top: 10px;
    padding-left: 10px;
    border-left: 2px solid #333;
    font-size: large;
    font-family:Arial, Helvetica, sans-serif;
  }
  /* Add these styles for the arrow */
.faq-item .question-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.faq-item .arrow {
  font-size: 1rem;
  transition: transform 0.3s ease;
}

.faq-item.active .arrow {
  transform: rotate(180deg);
}



@media screen and (max-width: 100%) {
    .faq .container {
        width: 100%;
    }

    .faq .container .content {
        grid-template-columns: 1fr;
    }

    .faq .card {
        max-width: 100%;
        margin: 1rem;
    }
}


  
  











