

.hero {
    color: #1C0028;
    height: 100vh;
    min-width: 960px;
    position: relative;
    background-image: url('./images/background.jpg'); /* Update the background image URL */
    background-size: cover; /* or your preferred background size */
    display: flex;
}



.hero .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
    padding: 8rem;
}

.hero .content p:first-child {
    font-size: 30px;
    font-family: 'TheBold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;
}

.hero .content p:nth-child(2) {
    font-size: 50px;
    font-weight: 800;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.hero .content p:nth-child(3) {
    font-size: 30px;
    font-family: 'TheBold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;
    padding-top: 3%;
}

.hero .button{
    border-radius: 40px;
    background: #FF70BF;
    color: #1C0028;
    margin-top: 4%;
    padding: 3%;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: medium;
}

.hero .button:hover {
    background: #f7cee4;
    border: 1px solid var(--secondary-dark);
    color: #333;
}

.hero .image {
    max-width: 35%;
    margin-right: 10%;
    display: flex;
    align-items: center;
    padding: 70px;
}

.hero .image img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
}

.hero .image .button.schedule-button {
    margin: 1rem;
    width: 100px;
    border-radius: 40px;
    background: #FF008C;
    color: #1C0028;
}


.hero .image .schedule-button:hover {
    background-color: #1C0028;
    border: 1px solid var(#1C0028);
}

/* Media Queries for Responsive Design */

@media screen and (max-width: 767px) {
    .hero .content {
        padding: 4rem;
    }

    .hero .content p:first-child {
        font-size: 25px;
    }

    .hero .content p:nth-child(2) {
        font-size: 40px;
    }

    .hero .content p:nth-child(3) {
        font-size: 20px;
    }

    .hero .button {
        width: 200px;
    }

    .hero .image {
        max-width: 100%;
        margin: 0;
        padding: 20px;
        justify-content: center;
    }
}

