/* Sponsors.css */

.sponsors {
    background-color: white;
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    min-width: 960px;
}

.sponsors .container {
    max-width: 1240px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 0.5rem; /* Adjust the gap between rows */
}

.sponsors h2 {
    font-size: 3rem;
    text-align: left;
    color: black;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    margin-left: 10%;
}

.sponsors .col-1 {
    max-width: 100%;
    align-content: center;
}

.sponsors h5{
    font-family: Arial, Helvetica, sans-serif;
    margin-left: 10%;

}

.sponsors p {
    text-align: left;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: large;
    padding-top: 5%; /* Adjust the padding between paragraphs */
    color: #43055D;
    margin-left: 3%;
}



/* Images styling with adjusted padding and centered position */
.image-container {
    display: grid;
    grid-gap: 1rem; /* Adjust the gap between rows */
    justify-content: center;
    align-items: center;
    padding-bottom: 6%; /* Adjust the value as needed */
}

.image-row {
    display: flex;
    justify-content: space-between;
    padding: 1%;
}

.square {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
}

.square img {
    width: 70%;
    height: auto;
    display: block;
}
