.testimonials {
    min-width: 960px;    background-color: #1C0028;
}

.testimonials .container {
    width: 100%;
    max-width: 1200px; /* Increase max-width for a wider container */
    margin: 0 auto;
    padding: 2rem 2rem; /* Adjust padding for responsiveness */

    /* Add some space at the sides to prevent text from touching the edges */
    box-sizing: border-box;
}

.testimonials h2 {
    font-size: 2rem;
    text-align: center;
    color: #ffffff;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.testimonials p{
    font-family: Arial, Helvetica, sans-serif;
    text-align: justify;
}
.testimonials p2{
    font-family: Arial, Helvetica, sans-serif;
    font-style: italic;
    color: white;
    font-weight: 600;
    text-align: righ !important;
}

.testimonials .container .content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(260px, 2fr));  /* Responsive grid layout */
    grid-gap: 36px;
    margin: 4rem 0;
}

.testimonials .card {
    border: 1px solid #6E88FF;
    background-color: #6E88FF;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgb(0, 0, 0, .2);
    padding: 1rem;
}

.testimonials img {
    max-width: 40%;
    margin-top: -2rem;
}

.testimonials .card p {
    color: #ffffff;
    font-size: 1.1rem;
    margin: 12px;
    font-family: Arial, Helvetica, sans-serif;
}

.testimonials .card span {
    color: #6E88FF;
    font-size: 1rem;
    font-weight: 00;
}

@media screen and (max-width: 100%) {
    .testimonials .container {
        width: 100%;
    }

    .testimonials .container .content {
        grid-template-columns: 1fr;
    }

    .testimonials .card {
        max-width: 100%;
        margin: 1rem;
    }
}


















